.canvas {
  background-color: white;
  position: relative;
}
.snake {
  background-color: rgba(0,0,0,0.5);
  border: thin solid black;
  position: absolute;
}
.food {
    background-color: rgba(0,0,255,0.5);
    border: thin solid black;
    position: absolute;
    border-radius: 50%;
  }
.button {
    background-color: rgba(0,0,0,0);
    position: absolute;
}

.button-ArrowRight {
    right: 0;
    top: 20%;
    height: 60%;
    width: 120px;
}

.button-ArrowDown {
    bottom: 0;
    right: 20%;
    width: 60%;
    height: 120px;
}
.button-ArrowLeft {
    left: 0;
    bottom: 20%;
    height: 60%;
    width: 120px;
}
.button-ArrowUp {
    left: 20%;
    width: 60%;
    height: 120px;
}
.app {
    margin-left: -8px;
    margin-top: -8px;
    width: 100vw;
    height: 96vh;
    background: #eee;
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: center;
    font-family: sans-serif;
    font-size: 14px;
}
